figure.mainSlider{
  width: 100%;

  .slide{
    width: 100%;
    height: 100%;
    overflow: hidden;

    ul{
      width: calc(100% * 3);
      height: 100%;
      background-color: $color-black;

      li{
        float: left;
        width: calc(100% / 3);
        height: 100%;
        position: relative;

        &::after{
          content: '';
          display: block;
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          background-color: $color-dark-gray;
          filter: blur(3px);
        }

        @for $i from 1 through 3 {
          &:nth-of-type(#{$i})::after{
            background: url('../../img/mainslide_#{$i}.jpeg') no-repeat center/cover;
          }
        }

        .inner{
          height: 100%;
          position: relative;
          z-index: 1;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;

          h2{
            font-size: 50px;
            font-weight: 100;
            line-height: 1.5;
            text-align: center;
            color: $color-white;
            text-shadow: 0 5px 20px rgba($color: $color-black, $alpha: 1);
            position: relative;

            span{
              font-weight: 700;
            }

            // &::after{
            //   content: '';
            //   display: block;
            //   width: 200px;
            //   height: 1px;
            //   background-color: $color-white;
            //   position: absolute;
            //   top: calc(100% + 50px);
            //   left: 50%;
            //   transform: translateX(-50%);
            // }
          }
          p{
            margin-top: 100px;
            font-family: Arial, Helvetica, sans-serif;
            font-size: $font-size-medium;
            line-height: 1.5;
            text-align: center;
            color: $color-white;
            text-shadow: 0 2px 10px rgba($color: $color-black, $alpha: 1);
          }
        }
      }
    }
  }

  >.inner{
    width: auto;
    position: absolute;
    top: 55%;
    left: 50%;
    z-index: 3;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;

    span{
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: $font-size-largest;
      color: $color-white;
      cursor: pointer;
    }
  }
}